.customer_box{
    width: 100%;
    height: 100%;  
}
.customer_connent_box{
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
}
.customer_connent{
    height: 60px;
    width: 1200px;
}
.customer_connent h3{
    width: 33.3%;
    height: 60px;
    line-height: 60px;
    padding: 0 30px;
    float: left;
    color: #333;
    font-weight: normal;
    margin-bottom: 0;
}
.customer_connent_inpt{
    width: 33.3%;
    height: 30px;
    line-height: 30px;
    margin-top: 15px;
    position: relative;
    float: left;
} 
.customer_connent_inpt input{
    border: 1px solid #ddd;
    width: 100%;
    height: 100%;
    padding-left:8px;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.customer_connent_inpt span{
    width: 38px;
    height: 100%;
    background-color: #F5F7FA;
    position: absolute;
    right: 0;
    top: 0;
    text-align: center;
    color: #606266 !important;
    border: 1px solid #ddd; 
    margin-right: 0 !important;
}
.customer_connent_new{
    width: 25%;
    float: right;
    position: relative;
}
.customer_connent_new_div1{
    margin-top: 15px;
    color: #fff;
    background-color: #3e84e9;
    width: 78px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    float: left;
    margin-left: 30px;
    position: absolute;
    right: 90px;
}
.customer_connent_new_div2{
    width: 60px;
    height: 30px;
    margin-top: 15px;
    border: 1px solid #dcdfe6;
    background-color: #fff;
    font-size: 13px;
    color: #777;
    cursor: pointer;
    padding: 0 12px;
    border-radius: 2px;
    line-height: 30px;
    text-align: center;
    margin-left: 10px;
    position: absolute;
    right: 0;
}
.customer_connent_new_none{
    width: 55px;
    height: 102px;
    position: absolute;
    bottom: -110px;
    border: 1px solid #ddd;
    background-color: #fff;
    left: 2px;
    z-index: 14;
}
.customer_connent_new_none p:first-child{
    margin-top: 10px;
}
.customer_connent_new_none p{
    line-height: 36px;
    text-align: center;
}
.customer_connent_new_show{
    display: none;
}
