/* contract_connent_css */
.contract_connent_css{
    width: 100%;
}
.contract_connent_css>p{
    padding-left: 5px;
    border-left: 1px solid #46CDCF;
    font-size: 12px;
}
.contract_connent_css>p>span{
    padding: 0 8px;
    border-radius: 10px;
    font-size: 10px;
    background-color: #FD715A;
    color: #fff;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


.dashboard_poso_poso{
    width: 100%;
    height: 100%;
}
.dashboard_contact_baccol{
    background-color: #fff;
    z-index: 100;
}
.dashboard_box_show_div1{
    height: 60px;
    line-height: 60px;
    color: #333;
}
.dashboard_box_show_div1 h3{
    float: left;
    font-weight: normal;
    padding: 0 20px;
    font-size: 18px;
    margin: 0;
}
.dashboard_box_show_div1_box1{
    width: 300px;
    height: 36px;
    line-height: 34px;
    border: 1px solid #ddd;
    color: #ddd;
    float: left;
    margin-left: calc(33.3% - 100px);
    position: relative;
    margin-top: 13px;
}
.dashboard_box_show_div1_box1 input{
    width: 100%;
    height: 100%;
    padding-left: 5px;
    box-sizing: border-box;
}
.dashboard_box_show_div1_box1 span{
    width: 38px;
    text-align: center;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    background-color: #F5F7FA;
    color: #ACAFB5 !important;
    border-left: 1px solid #ddd;
    margin-right: 0;
}
.dashboard_box_show_div1_box2{
    font-size: 37px;
    margin-right: 10px;
    height: 100%;
    color: #CECECE !important;
    float: right;
}
.dashboard_box_show_div2{
    height: 48px;
    line-height: 48px;
    border: 1px solid #E6E6E6;
    background-color: #FAFDFF;
    overflow: hidden;
}
.dashboard_box_show_div2_box1{
    float: left;
    border-right: 1px solid #E6E6E6;
    padding: 0 20px 0 10px;
    position: relative;
}
.dashboard_box_show_div2_box1 span{
    color: #C0C4CC !important;
    right: -5px;
    position: absolute;
    font-size: 12px !important;

}
.dashboard_box_show_div2_box1_span1{
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    bottom: 6px;
}
.dashboard_box_show_div2_box1_span2{
    top: 6px;
}
.dashboard_box_show_div2_box1_flexd{
    width: 100%;
    border-top:1px solid #E6E6E6 ;
}
.dashboard_box_show_div2_box1_mag{
    margin-right: 10%;
}
.high_connent_new_hidden{
    display: none;
}