.hbfb{
    height: calc(100% - 68px);
    position: relative;
    display: flex;
}
.bag{
    position: relative;
    float: left;
    height: 100%;
    width:200px;
    background-color: #2d3037 !important;
    transition: all 0.4s;
    margin-top: -2px;
}
.bagact{
    float: left;
    position: relative;
    height: 100%;
    width:63px;
    background-color: #2d3037 !important;
    transition: all 0.1s;
}
.bag button{
    position: absolute;
    background: none !important;
    border: none;
    right: 10px !important;
    bottom: 10px !important;
}
.bagact button{
    position: absolute;
    background: none !important;
    border: none;
    right: 10px !important;
    bottom: 10px !important;
}
.bag button:hover{
    background: none !important;
}
.ul{
    height: 200px;
    background-color: transparent !important;
}
.ant-menu-inline-collapsed{
    width: 63px !important;
}
.ant-menu-item:hover{
    color: #fff !important;
    background-color: #24262c;
}
.ant-btn-primary:hover, .ant-btn-primary:focus{
    background: none !important;
    border:none !important;
}
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
    background-color: #454e57 !important;
    border-left: solid 3px #3e84e9;
    box-sizing: border-box;
}
.ant-menu-item-selected {
    color: #fff !important;
}
.iconfont{
    color: #fff;
    font-size: 14px;
}
.sp{
    float: right;
}
.kjcj-n p{
    margin-bottom: 0;
}
.kjcj-n p em{
    margin-right: 10px;
    color: #78a9f0;
}
.lp{
    float: left;
}
.kjcj-none{
    display: none;
}
.kjcj-n{
    width: 80%;
    background-color: #3e84e9;
    border-radius: 4px;
    color: #fff;
    text-align: center;
    box-sizing: border-box;
    padding: 7px 15px;
    margin: 0 auto;
}
.kjcj{
    width: 100%;
    padding: 10% 0;
}
.tc{
    z-index: 99999 !important;
}
.ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
    border-right: none !important;
}
.a{
    display: inline-block;
    width: 100%;
    height: 100%;
    color: #fff;
}
li{
    position: relative;
}
li a{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
}

.content{
    float: left;
    width: 100%;
    height: 100%;
}
.content_box{
    height: 100%;
    background: #f5f6f9;
    flex: 1;
}
.ant-menu{
    color: #fff !important;
}
.tc>div{
    text-align: center;
    width: 90px;
    display: inline-block;
    line-height: 30px;
    background-color: #f7f8fa;
    border: solid 1px #f2f2f2;
    margin-bottom: 20px;
    border-radius: 3px;
    color: #333 !important;
}
.tc>div .iconfont{
    color: #333;
    font-size: 18px;
}
.tc>div:hover{
    background-color: #3e84e9;
    color: #fff !important;
}
.tc>div:hover .iconfont{
    color: #fff;
}
.kjcj:hover .tc{
    display: block;
}
.tca{
    margin-right: 20px;
}
.tc{
    width: 250px;
    position: absolute;
    padding: 20px 20px 0 20px;
    top: 20px;
    left: 100%;
    box-shadow: 3px 3px 4px #ddd;
    background-color: #fff;
    display: none;
}