.archive_box{
    width: 100%;
    height:100%;
    padding:15px;
}
.archive_box_t{
    width:100%;
    height: 60px;
    padding: 0 20px;
    position: relative;
}
.archive_box_title{
    color:#333;
    font-size:18px;
    line-height: 60px;
    display:inline-block;
    margin-right:10px;
}
.archive_box_t img{
    width:15px;
    position: absolute;
    top:24px;
}
.archive_box_content{
    position: relative;
    width:100%;
    height:90%;
    background-color: #fff;
    border: 1px solid#e6e6e6;;
    border-radius:3px;
}
.archive_box_content_img{
    width:1300px;
    height:154px;
    top: 50%;
    width: 100%;
    text-align: center;
    position: absolute;
}
.archive_box_content_img img{
    display: block;
    width: 150px;
    margin: 0 auto 20px;

}
.archive_box_content_img_p{
    margin: 3px 0;
    color: #aaa;
    font-size: 13px;
}
.label_arr_btn_hidden{
    display: none;
}
.label_arr_btn{
    width: 300px;
    position: absolute;
    top: 45px;
    z-index: 1010;
    left: 70px;
    padding: 10px;
    box-sizing: border-box;
    color: #333;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    background-color: #fff;
}
.label_arr_btn h3{
    margin: 0 0 20px;
    padding: 10px;
    box-sizing: border-box;
    border-bottom: 1px solid #e6e6e6;
    font-size: 13px;
}
.label_arr_num{
    width: 15px;
    height: 15px;
}
.label_arr_width{
    width: 100%;
}
.label_bottom_baoc{
    width: 100%;
    height: 36px;
    margin: 20px;
}
.label_bottom_baoc>div{
    float: left;
    width: 67px;
    height: 34px;
    line-height: 34px;
    text-align: center;
    font-size: 13px;
}
.label_bottom_baoc>div:first-child{
    background-color: #3E84E9;
    border: 1px solid #3E84E9;
    color: #fff;
    margin-right: 15px;
    margin-left: 100px;
}
.label_bottom_baoc>div:last-child{
    border: 1px solid #ddd;
    background-color: #fff;
}