@charset "utf-8";
*{ margin: 0; padding: 0; }
ul,ol { list-style: none }
img{ vertical-align: top; }
input, button{ outline: none; border: none; background-color: transparent; }
em, i{ font-style: normal; }
a{ text-decoration: none; }
.clearfix::after,.clearfix::before{content:'';display:table;}.clearfix::after{clear:both;}

.box{
  width: 100%;
  display: flex;
  position: relative;
}
img{
  width: 100%;
}
.boximg{
  flex: 7;
}
.inp{
  flex: 3;
  padding-top: 6%;
  position: relative;
}
.inpnr{
    width: 70%;
    margin: 0 auto;
}
.h1{
    font-size: 26px;
    color: #333;
    margin: 0 auto 50px;
    text-align: center;
    font-family: MicrosoftYaHei,Avenir,Helvetica,Arial,sans-serif;
    font-weight: normal;
}
.inpp input{
    width: 100%;
    height: 40px;
    padding: 0 12px;
    border-bottom: 1px solid #e6e6e6!important;
    margin-bottom: 20px;
    box-sizing: border-box;
}
.inpp button{
    background-color: #0ae;
    width: 100%;
    line-height: 48px;
    font-size: 16px;
    color: #fff;
    border-radius: 3px;
    background-color: #0ae;
}
.inpimg{
  position: absolute;
  left: 60px;
  top: 50px;
}
.inpimg img{
  width: 180px;
}
.p{
  width: 92%;
  position: absolute;
  bottom: 2%;
  color: #d0d0d0;
  font-size: 12px;
  text-align: center;
  line-height: 1.5;
  left: 4%;
}