.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.logo{
  width: 200px;
  display: inline-block;
}
.logo img{
  width: 150px;
}
.tb{
  padding: 10px 30px;
  box-shadow: 0 1px 2px #dbdbdb !important;
  margin-bottom: 2px;
}
.iconfont{
  margin-right: 10px;
}
.tite{
  color: rgb(51, 51, 51);
  padding: 0 30px;
  line-height: 40px;
}
.active{
  color: #3e84e9;
  padding: 0 30px;
  line-height: 40px;
}
.uo{
  height: 100%;
}
.tb .iconfont{
  color: #333333;
}
.active .iconfont{
  color: #3e84e9;
}
.app-tc{
  float: right;
  line-height: 40px;
}
.recycle-fixed-ct-r-gl{
  float: left;
}
.recycle-fixed-ct-r-p{
  float: left;
}