.customertotal{
    margin: 15px;
}
.customertotal{
    background-color: #fff;
    border: solid 1px #e6e6e6;
    padding: 15px;
}
.customertotal-xlk{
    margin-right: 20px;
}
.customertotal-xlkk{
    width: 130px;
    margin-right: 20px;
}
.customertotal-title-xlk-tc{
    height: 70px;
}
.customertotal-title-xlk-tc1{
    top: 110% !important;
}
.customertotal-ti-btn{
    color: #fff;
    background-color: #3e84e9;
    border-color: #3e84e9;
    height: 34px;
    padding: 0 14px;
    border-radius: 3px;
}
.customertotal-content-n{
    width: 100%;
    height: 400px;
}
.customertotal-content-bg-c{
    color: #909399;
}
.customertotal-content-bg-w{
    border-top: solid 1px #e6e6e6;
    border-left: solid 1px #e6e6e6;
    overflow: auto;
    height: 500px;
}
.customertotal-content-bg{
    display: flex;
}
.customertotal-content-bg>div{
    flex: 1;
    text-align: center;
    border-bottom: solid 1px #e6e6e6;
    line-height: 48px;
    height: 48px;
    border-right: solid 1px #e6e6e6;
}
.customertotal-content-no{
    height: 600px;
    overflow: auto;
}
.customerrate-txt{
    width: 100%;
    text-align: center;
    color: #909399;
}
.customerrate-txt>p{
    width: 100%;
    line-height: 100%;
    line-height: 300px;
}
.customerdeal-p{
    font-size: 16px;
    margin: 20px 0 0 90px;
}
.customertotal-content-n-img{
    display: flex;
    margin-bottom: 60px;
}
.customertotal-content-n-img>div{
    flex: 1;
}
.customertotal-content-n-img-w img{
    width: 510px;
    margin: 0 0 0 60px;
}