.dashboard{
    padding: 15px;
    overflow: auto;
    height: 100%;
}
.dashboard-tite{
    height: 40px;
    margin-bottom: 20px;
}
.dashboard-iconfont{
    float: left;
    margin-right: 16px;
}
.dashboard-iconfont .iconfont{
    height: 40px;
    width: 40px;
    background-color: #fd8f70;
    text-align: center;
    border-radius: 50%;
    display: inline-block;
    line-height: 40px;
    font-size: 18px;
    color: #fff;
    text-align: center;
    margin-right: 0;
}
.dashboard-title-h1{
    float: left;
}
.dashboard-title-h1 h1{
    font-size: 16px;
    color: #333;
    line-height: 40px;
    margin-bottom: 0;
}
.dashboard-title-xg{
    float: left;
    margin: 0 14px;
    color: #aaa;
    line-height: 40px;
}
.dashboard-title-xlk{
