    z-index: 99;
}
.dashboard-title-qh{
    float: left;
    margin-right: 15px;
}
.dashboard-title-xlk-tc{
    height: 250px;
    overflow: auto;
}
.dashboard-content-h-lg-l{
    height: calc(100% - 36px);
    position: relative;
}
.l{
    float: left;
}
.dashboard-content-h-lg-l-p{
    position: absolute;
    top: calc(50% - 30px);
    left: 40px;
}
.dashboard-content-h-lg-l-p p{
    line-height: 24px;
    font-size: 13px;
    
}
.dashboard-title-xlk .dashboard-title-xlk-tc{
    top: 110%;
}
.dashboard-title-xlk-tc-sj-span{
    position: relative;
}
.dashboard-title-xlk-tc-sj-span span{
    position: absolute;
    left: 8px;
    top: 50%;
    margin-top: -4px;
    width: 8px;
    height: 8px;
    background-color: #3e84e9;
    border-radius: 50%;
}
.dashboard-title-qh p{
