/* 阶段变化的商机 */
.dashboard_business_box{
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff !important;
    width: 100%;
    height: 100%;
    z-index: 999;
}
.dashboard_business_box_cont h3{
    font-size: 18px;
    padding: 0 20px;
    line-height: 60px;
    width: 100%;
    position: relative;
}
.dashboard_business_box_cont h3 span{
    position: absolute;
   right: 0;
   top: 0;
   margin-left: 20px;
    /* margin-top: 10px; */
    margin-right: 10px;
    padding: 10px;
    color: #e4e4e4;
    font-size: 30px;
}
.dashboard_business_box_from{
    border-top: solid 1px #e6e6e6;
}
.dashboard_business_box_from li,
.dashboard_business_box_from_title{
    font-size: 14px;
    color: #475059;
    line-height: 23px;
    padding: 0;
    line-height: 48px;
    border-bottom: solid 1px #e6e6e6;
}

.dashboard_business_box_from li:nth-child(2n){
    background-color: #fafafa;
}
.dashboard_business_box_from li:nth-child(1){
    background-color: #fafdff;
}
.dashboard_business_box_from li:hover{
    background-color: #ecf3fd;
}
.dashboard_business_box_from .dashboard_business_box_from_active{
    background-color: #ecf3fd !important;
}
.dashboard_business_box_from li p,
.dashboard_business_box_from_title p{
    width: 300px;
    float: left;
    padding-left: 10px;
    border-right: solid 1px #e6e6e6;
}
.dashboard_business_box_from li div,
.dashboard_business_box_from_title div{
    width: calc(100% - 300px);
    padding-left: 10px;
    margin-left: 300px;
}