@font-face {
    font-family: 'iconfont';
    src: url('../font/iconfont.eot');
    src: url('../font/iconfont.eot?#iefix') format('embedded-opentype'),
        url('../font/iconfont.woff2') format('woff2'),
        url('../font/iconfont.woff') format('woff'),
        url('../font/iconfont.ttf') format('truetype'),
        url('../font/iconfont.svg#iconfont') format('svg');
}

.iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
.todolist {
    width: 100%;
    padding: 15px;
    height: 100%;
    box-sizing: border-box;
}
.filtrate-button_el-dialog__body>div:nth-child(2){
    min-height: 300px;
    overflow: auto;
}
.todolist_box {
    width: 100%;
    height: 100%;
    /* overflow: hidden; */
    color: #333;
    font-size: 13px;
}

.todolist h3 {
    width: 100%;
    height: 60px;
    font-size: 18px;
    padding: 0 20px;
    line-height: 60px;
}

.todolist_cont_box {
    height: calc(100% - 70px);
}

/* 侧边栏 */
.todolist_nav_box {
    width: 200px;
    font-size: 14px;
    background-color: #fff;
    float: left;
    bottom: 0;
    z-index: 1;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    overflow-y: auto;
    height: 100%;
}

.todolist_nav_list>div {
    height: 50px;
    line-height: 50px;
    padding: 0 20px;
}

.todolist_nav_list .todolist_nav_list_active {
    color: #409eff;
    border-right: 2px solid #46cdcf;
    background-color: #ecf5ff;
}


/* 右边 */
.todolist_cont_detail {
    margin-left: 210px;
    height: 100%;
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    position: relative;
    width: 1000px;
}

.todolist_cont_detail-box {
    height: calc(100% - 160px);
    overflow-y: hidden;
    overflow-x: scroll;
    white-space: nowrap;
    margin-top: 120px;
    border-top: solid 1px #e6e6e6;
}

.todolist_cont_detail-title-box {
    position: absolute;
    left: 0;
    top: 0;
}

.todolist_cont_detail_title {
    padding: 0 20px;
    font-size: 16px;
    line-height: 60px;
    height: 60px;
}

.todolist_cont_detail_title_ic {
    margin-left: 5px;
    color: #dcdfe6;
}

.todolist_cont_detail_option-bar_box {
    padding: 5px 20px;
}

.todolist_cont_detail_option-bar {
    position: relative;
    border-radius: 3px;
    height: 34px;
    line-height: 34px;
    border: solid 1px #dddddd;
}

.option-bar-width-1 {
    width: 192px;
    float: left;
}

.option-bar-width-2 {
    width: 120px;
    float: left;
    margin-left: 10px;
}

.option-bar-width-1 input {
    padding: 0 30px 0 8px;
    width: 100%;
}

.option-bar-width-2 input {
    padding: 0 30px 0 8px;
    width: 100%;
}

.todolist_cont_detail_option-bar-ic span {
    position: absolute;
    right: 0;
    top: 3px;
}

.todolist_cont_detail_option-bar-ic .iconfont {
    font-size: 25px;
    color: #c0c4cc;
    margin-right: 0;
}

.filtrate-button {
    height: 34px;
    line-height: 34px;
    font-size: 14px;
    margin-left: 20px;
}

.filtrate-button .iconfont {
    color: #b0b0b0;
    margin-right: 5px;
    margin-left: 20px;
}

.filtrate-button:hover {
    color: #3e84e9;
}

.filtrate-button-box {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgb(0 0 0 / 10%);
    z-index:999;
}

.filtrate-button-cont-box {
    width: 900px;
    margin: 15vh auto;
    background-color: #fff;
}

.filtrate-button-cont-box h4 {
    padding: 20px 20px 10px;
    color: #333;
    font-size: 14px;
    width: 100%;
    position: relative;
}

.filtrate-button-cont-box h4 .iconfont {
    color: #909399;
    position: absolute;
    right: 10px;
    top: 15px;
}

.filtrate-button_el-dialog__body {
    padding: 10px 20px;
}

.filtrate-button_el-dialog__body_title {
    color: #333;
    font-size: 14px;
    margin-bottom: 10px;
}

.filter-container_box {
    display: flex;
    margin-bottom: 30px;
}

.filter-container_input {
    border: solid 1px #dddddd;
    position: relative;
    line-height: 34px;
}

.filter-container_input input {
    width: 100%;
    padding: 0 30px 0 8px;
}

.filter-container_input_w,
.filter-container_input_position_1 {
    width: 286px;
}

.filter-container_input_position_2 {
    width: 143px;
}

.filter-container_input_z {
    width: 143px;
    margin: 0 35px;
}

.filtrate-button_el-dialog__body .iconfont {
    color: #c0c4cc;
}

.filtrate-button_el-dialog__body .filter-con_iconfont {
    color: #fff;
}

.filter-con_iconfont {
    position: absolute;
    right: -50px;
    bottom: 10px;
    width: 14px;
    height: 14px;
    background-color: #bbbbbb;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    border-radius: 50%;
}

.filter-container_input_position {
    position: absolute;
    background-color: #fff;
    width: 100%;
}

.filter-container_input_position_1,
.filter-container_input_position_2 {
    max-height: 274px;
    font-size: 12px;
    white-space: nowrap;
    overflow-y: scroll;
    color: #333;
    line-height: 34px;
    border: solid 1px #e4e7ed;
}

.position_1_bottom_1>div>div {
    padding: 0 20px;
}

.position_1_bottom_1>div:hover {
    background-color: #f5f7fa;
}

.position_1_bottom_1 {
    position: absolute;
    bottom: -280px;
    left: 0;
    background-color: #fff;
    z-index: 2;
}

.todolist-button--text {
    padding: 8px 0;
    color: #3e84e9;
    font-size: 13px;
    line-height: 17px;
}

.dialog-footer {
    padding: 10px 20px 20px;
    text-align: right;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.dialog-footer>div {
    display: inline-block;
    padding: 8px 12px;
    font-size: 12px;
    border-radius: 3px;

}

.dialog-footer-qx {
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #ddd;
    color: #333;
}

.dialog-footer-qd {
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    margin-left: 10px;
    color: #fff;
    background-color: #3e84e9;
    border-color: #3e84e9;
}

.dialog-footer-qx:hover {
    background-color: #ecf3fd;
    color: #3e84e9;
    border-color: #c5daf8;
}

.dialog-footer-qd:hover {
    background-color: #659ded;
}




.todolist_cont_input_el-scrollbar {
    position: absolute;
    left: 0;
    width: 100%;
    font-size: 12px;
    background-color: #fff;
    border: solid 1px #e4e7ed;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
}

.input_el-scrollbar_bottom_1 {
    bottom: -110px;
}

.input_el-scrollbar_bottom_2 {
    bottom: -75px;
}

.input_el-scrollbar_bottom_3 {
    top: -145px;
    text-align: left;
}

.input_el-scrollbar_bottom_h {
    height: 30px;
    line-height: 30px;
}

.todolist_cont_input_el-scrollbar_title {
    padding: 0 20px;
}

.todolist_cont_has-gutter {
    width: 100%;
    display: flex;
}

.todolist_cont_has-gutter_title {
    background-color: #fafdff;
    font-weight: 400;
    border-color: #e6e6e6;
    color: #475059;
    padding: 12px 20px;
    border-right: solid 1px #e6e6e6;
    border-bottom: solid 1px #e6e6e6;
}

.todolist_cont_detail-box_bottom {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 44px;
    line-height: 44px;
    text-align: right;
    width: 100%;
}

::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}

::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}

::-webkit-scrollbar-track-piece {
    background: #efefef;
}

.todolist_cont_detail-box_bottom_page {
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    vertical-align: top;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin-right: 10px;
    margin-top: 5px;
}

.todolist_cont_detail-box_bottom_page_xz {
    display: flex;
}

.todolist_cont_bottom_marg {
    margin-left: 20px;
    margin-right: 20px;
}

.todolist_cont_detail-box_bottom .btn-prev {
    color: #c0c4cc;
    background-color: #fff;
    cursor: not-allowed;
    padding: 0 12px;
    display: inline-block;
}

.todolist_cont_el-pager {
    color: #3e84e9;
    cursor: default;
    padding: 0 4px;
    background: #fff;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    text-align: center;
    margin: 0;
}

.todolist_el-pagination__editor {
    width: 46px;
    height: 28px;
    padding: 0 3px;
    line-height: 28px;
    border: solid 1px #e6e6e6;
    border-radius: 5px;
    display: inline-block;
}

.todolist_el-pagination__editor input {
    text-align: center;
    display: inline-block;
    width: 100%;
}

.todolist_el-pagination__jump {
    margin: 0 5px;
    line-height: 44px;
    display: inline-block;

}

.martop {
    margin-top: 0;
}

.el-table__empty-text {
    text-align: center;
    line-height: 60px;
    color: #909399;
    margin-top: 100px;

}




/* 组件 */
.customer_scene_management {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(0 0 0 / 20%);
    width: 100%;
    height: 100%;
}

.customer_scene_management_box {
    margin-top: 15vh;
    width: 700px;
    background: #fff;
    margin: 15vh auto 0 auto;
}

.customer_scene_management_box h4 {
    padding: 20px 20px 10px;
    font-size: 16px;
    position: relative;
    width: 100%;
}

.customer_scene_management_box h4 .iconfont {
    position: absolute;
    right: 0;
}

.customer_scene_management .iconfont {
    color: #909399;
}

.customer_scene_management_drag_box {
    padding: 20px 20px 10px;
}

.scene-name {
    font-size: 12px;
    padding-bottom: 5px;
    color: #aaa;
}

.customer_manage_tags_box {
    display: flex
}

.customer_manage_tags {
    flex: 1;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    height: 300px;
    padding: 15px;
    overflow: auto;
}

.customer_manage_tags_button {
    width: 50px;
}

.customer_manage_tags_title {
    display: flex;
    font-size: 13px;
    border-bottom: 1px dashed #e6e6e6;
    padding: 0 0 15px 9px;
}
.manage_tags_title_input{
    margin-right: 30px;
}
.manage_tags_title_input span {
    display: inline-block;
    position: relative;
    border: 1px solid #ddd;
    border-radius: 2px;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    /* background-color: #fff; */
    z-index: 1;
}
.manage_tags_title_input_xz,
.manage_tags_title_input_sxz{
    background-color: #3e84e9;
}

.manage_tags_title_input_xz::after,
.manage_tags_title_input_sxz::after{
    content: "";
    border: 1px solid #fff;
    border-left: 0;
    border-top: 0;
    height: 7px;
    width: 4px;
    position: absolute;
    top: 1px;
    left: 4px;
    transform: rotate(45deg) scaleY(1);
}

.manage_tags_title_input input {
    opacity: 0;
    outline: none;
    position: absolute;
    margin: 0;
    width: 0;
    height: 0;
    z-index: -1;
}
.customer_manage_tags_title h6{
    flex: 1;
}

.manage_tags_title_amount{
    color: #aaa;
    text-align: right;
}
.manage_tags_list{
    display: flex;
    padding: 5px 0 5px 9px;
    cursor: pointer;
}
.manage_tags_list_input{
    margin-right: 8px;
}
.manage_tags_list_input-cursor input:hover{
    cursor:not-allowed;
    cursor:no-drop;
}
.customer_manage_tags_button{
    margin-top: calc(300px / 2 - 40px);
}
.customer_manage_tags_button>span{
    display: block;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border: 1px solid #ccc;
    color: #c0c4cc;
    line-height: 34px;
    text-align: center;
    margin: 0 auto;
}
.customer_manage_tags_button .customer_manage_tags_button-left{
    margin-bottom: 15px;
}
.customer_manage_tags_button .customer_manage_tags_button_path{
    background-color: #3e84e9;
    color: #fff;
}
.handle-bar-add{
    cursor: pointer;
    color: #3e84e9;
    padding: 20px 10px;
    font-size: 12px;
}



/* 导入 */
.cluesimport_box{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(0 0 0 / 20%) !important;
    width: 100%;
    height: 100%;
    z-index: 999;
}
.cluesimport_cont_box{
    background-color: #fff;
    width: 550px;
    margin: 15vh auto 0 auto;
}
.cluesimport_cont_box h3{
    padding: 20px 20px 10px;
    font-size: 16px;
    line-height: 18px;
    color: #303133;
    width: 100%;
    position: relative;
}
.cluesimport_cont_box h3 span{
    position: absolute;
    right: 10px;
    top: 10px;
    color: #909399;
    font-size: 12px;

}
.cluesimport_cont_title{
    display: flex;
}
.cluesimport_cont_title li{
    flex: 1;
    text-align: center;
    color: #c0c4cc;
    line-height: 40px;
    font-size: 14px;
}
.cluesimport_cont_title .cluesimport_cont_title_active{
    color: #3e84e9;
    position: relative;
}
.cluesimport_cont_title .cluesimport_cont_title_active span{
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -40px;
    width: 80px;
    height: 3px;
    background-color: #3e84e9;
}   
.cluesimport_cont_text{
    padding: 0 20px;
}
.cluesimport_cont_text h5{
    font-size: 14px;
    color: #333;
    line-height: 16px;
}
.cluesimport_cont_text h5 span{
    color: #3e84e9
}
.cluesimport_content_content-tips{
    font-size: 12px;
    color: #a9a9a9;
    line-height: 15px;
    padding: 10px 10px 10px 30px;
}
.cluesimport_el-input{
    padding: 10px 10px 10px 30px;
    width: 100%;
}
.cluesimport_el-input--suffix{
    width: 300px;
    line-height: 33px;
    border: solid 1px #dddddd;
    font-size: 14px;
    position: relative;
}
.cluesimport_el-input--suffix input{
    width: 100%;
    padding: 0 30px 0 8px;
}

.cluesimport_el-select-dropdown__list{
    position: absolute;
    bottom: -80px;
    left: 0;
    background-color: #fff;
    width: 100%;
    border:  solid 1px #e4e7ed;
}
.cluesimport_el-select-dropdown__list>div{
    font-size: 12px;
    padding: 0 20px;
    line-height: 34px;
    padding: 0 20px;
}
.cluesimport_el-select-dropdown__list>div:hover{
    background-color: #f5f7fa;
}
.cluesimport_el-select-dropdown__list>div:nth-child(1){
    color: #3e84ec;
}
.cluesimport_is-disabled_input{
    width: 300px;
    line-height: 33px;
    border: solid 1px #dddddd;
    float: left;
}
.cluesimport_is-disabled_input input{
    width: 100%;
    padding: 0 8px;
    background-color: #f5f7fa;
}
.cluesimport_is-disabled_input input:hover{
    cursor: not-allowed;
}
.cluesimport_is-disabled_btn{
    margin-left: 340px;
    width: 80px;
    line-height: 30px;
    margin-top: 3px;
    background-color: #3e84e9;
    text-align: center;
    color: #fff;
}
.user-container{
    width: 300px;
    line-height: 33px;
    border: solid 1px #dddddd;

}
.user-container input{
    padding: 5px;
    width: 100%;
}
.user-container input::-webkit-input-placeholder { 
    color: #3e84e9;
} 
.user-container_box{
    position: relative;
}   
.user-container_box .el-popover{
    position: absolute;
    bottom: -300px;
    width: 300px;
    border: solid 1px #ebeef5;
}
.cluesimport_botton_box{
    text-align: right;
    padding: 10px 20px 20px;
}
.cluesimport_botton_box>div{
    display: inline-block;
    margin: 0 5px;
    font-size: 13px;
    line-height: 30px;
    padding: 0 12px;
}
.cluesimport_botton_left{
    border: solid 1px #dddddd;
}
.cluesimport_botton_right{
    background-color: #3e84e9;
    color: #fff;
}