.employeenumber{
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
}
.employeenumber_box{
    width: 100%;
    overflow: auto;
    height: 100%;
    background-color: #fff;
}
.employeenumber_box_header{
    height: 54px;
    color: #333;
    margin: 0 15px 0 0;
    padding: 15px 20px 5px;
    box-sizing: border-box;
}
.employeenumber_box_header_box1{
    width: 130px;
    float: left;
    margin-right: 20px;
}
.employeenumber_box_header_button{
    height: 30px;
    line-height: 30px;
    background-color: #3E84E9;
    color:#fff;
    padding: 0 12px;
    float: left;
}
.employeenumber_box_connent,.EmployeenumberConnent_box{
    width: 100%;
    height: 460px;
}
.employeenumber_box_connent{
    padding: 20px 136px 12px 40px;
    box-sizing: border-box;
}
.employeenumber_box_biaoge{
    width: 100%;
    height: 320px;
    margin: 20px 0;
    box-sizing: border-box;
}
.employeenumber_box_biaoge_scroll{
    width: 1200px;
    height: 100%;
    margin: auto;
    overflow: auto;
}
.employeenumber_box_biaoge_box1{
    width: 1403px;
    height: 100%;
    background-color: yellow;
    border: 1px solid #e6e6e6;
}
.employeenumber_box_biaoge_box1>div:hover{
    background-color: #F5F7FA;
}
.employeenumber_biaoge_box1_div1{
    height: 47px;
}
.employeenumber_biaoge_box1_div1>div{
    float: left;
    width: 100px;
    text-align: center;
    padding: 12px 0;
    color: #909399;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
}
.employeenumber_biaoge_box1_div1>div:first-child{
    width: 200px;
}
.employeenumber_baccol_box1,.employeenumber_baccol_box2,.employeenumber_baccol_box4,.employeenumber_baccol_box6{
    background-color: #fff;
}
.employeenumber_baccol_box3,.employeenumber_baccol_box5{
    background-color: #FAFAFA;
}
.employeenumber_box_header_title{
    padding: 10px 20px 15px;
    color: #333;
}
.employeereceivable_box_biaoge_scroll{
    width: 100%;
    height: 650pxpx;
}
.employeereceivable_pading{
    width: 90%;
    height: 500px;
    overflow: auto;
    margin: 30px auto;
    border: 1px solid #e6e6e6;
}
.employeereceiveable_box{
    overflow: hidden;
}
.employeereceivable_box_biaoge_scroll>div>div{
    width: calc(25% - 1px);
    height: 47px;
    border-right: 1px solid #e6e6e6;
    border-top: 1px solid #e6e6e6;
    float: left;
    text-align: center;
    line-height: 47px;
}
.employeereceivable_box_biaoge_scroll>div{
    height: 47px;
}
.employeereceivable_baccol{
    background-color: #FAFAFA;
}
.employeereceivable_box_biaoge_scroll>div:hover{
    background-color: #F5F7FA;
}