.recycle-box{
    margin: 15px;
    height: calc(100% - 30px);
    position: relative;
}
.recycle-h3{
    line-height: 60px;
    font-size: 18px;
    padding: 0 20px;
}
.recycle-cnt{
    width: 100%;
    height: calc(100% - 60px);
    border: solid 1px #e6e6e6;
    background-color: #fff;
}
.recycle-ct-inp{
    height: 50px;
    padding: 0 10px;
    border-bottom: 1px #e6e6e6 solid;
}
.recycle-ct-inp input{
    float: left;
    width: 14px;
    height: 14px;
    margin: 18px 0;
    margin-right: 10px;
}
.recycle-ct-inp-bt{
    float: left;
    line-height: 50px;
    text-decoration: line-through;
}
.recycle-ct-inp-r{
    float: right;
}
.recycle-ct-inp-r .iconfont{
    color: #9fa19c;
    line-height: 50px;
    float: left;
}
.recycle-ct-inp-sj{
    float: left;
    line-height: 50px;
    font-size: 13px;
    color: #999;
    margin-right: 10px;
}
.recycle-ct-inp-img{
    width: 30px;
    height: 30px;
    margin: 10px 0;
}
.recycle-ct-inp-img img{
    border-radius: 50%;
    width: 100%;
}
.recycle-ct-inp-img-txt{
    width: 30px;
    height: 30px;
    background-color: #2486e4;
    font-size: .5em;
    line-height: 30px;
    color: #fff;
    overflow: hidden;
    font-size: 10px;
    text-align: center;
    border-radius: 50%;
}
.recycle-fixed{
    position: absolute;
    right: -15px;
    top: -15px;
    width: 60%;
    height: calc(100% + 25px);
    background-color: #fff;
    z-index: 99999999;
    border-left: solid 1px #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
}
.recycle-fixed-tb{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 56px;
    border-bottom: 1px solid #e6e6e6;
}
.recycle-fixed-tb-r{
    float: right;
    width: 100%;
}
.recycle-fixed-tb-r > .iconfont{
    position: absolute;
    right: 10px;
    top: 0;
    color: #cccccc;
    font-size: 26px;
    line-height: 56px;
}
ul{
    margin-bottom: 0 !important;
}
.recycle-fixed-tb-r ul{
    float: right;
    margin-right: 50px;
}
.recycle-fixed-tb-r ul .iconfont{
    color: #cccccc;
}
.recycle-fixed-tb-r ul li{
    display: inline-block;
    line-height: 56px;
    margin-right: 20px;
    color: #cccccc;
}
.recycle-fixed-ct{
    margin-top: 60px;
}
.recycle-fixed-ctt{
    display: flex;
    margin-bottom: 40px;
}
.recycle-fixed-ct input{
    width: 20px;
    height: 20px;
}
.recycle-fixed-ct{
    padding: 15px;
}
.recycle-fixed-ct-l{
    flex: 3;
}
.recycle-fixed-ct-r{
    flex: 1;
}
.recycle-fixed-ct-r img{
    width: 20px;
    height: 20px;
    border-radius: 50%;
}
.recycle-fixed-ct-l-bq{
    display: inline-block;
    line-height: 22px;
    padding: 0 4px;
    background-color: #2486e4;
    border-radius: 4px;
    color: #fff;
    font-size: 13px;
}
.recycle-fixed-ct-l input{
    float: left;
    margin-right: 4px;
}
.recycle-fixed-ct-l span{
    float: left;
    line-height: 20px;
}
.recycle-fixed-ct-l p{
    display: inline-block;
    margin-right: 30px;
}
.recycle-fixed-ct-l span{
    font-size: 13px;
}
.recycle-fixed-ct-l .iconfont{
    color: #2486e4;
    margin-right: 4px;
}
.recycle-fixed-ct-l-span{
    color: #2486e4;
}
.recycle-fixed-ct-l-qwe{
    width: 15px;
    height: 15px;
    display: inline-block;
    border: solid 1px #666666;
    border-radius: 50%;
    line-height: 15px;
    text-align: center;
    margin-left: -30px;
}
.recycle-fixed-ct-l-qwe{
    line-height: .8;
}
.recycle-fixed-ct-r-b-ys{
    width: 100%;
    height: 40px;
    background-color: #f5f7fa;
    line-height: 40px;
    color: #2486e4;
}
.recycle-fixed-ct-r-b-ys .iconfont{
    color: #2486e4 !important;
}
.recycle-fixed-ct-r-b-ys p{
    display: inline-block;
}
.recycle-fixed-ct-r-b-ys .iconfont{
    color: #666666;
    margin-left: 15px;
}
.recycle-fixed-ct-l-bka{
    display: inline-block;
    width: 83%;
    height: 4px;
    background-color: #f5f5f5;
}
