.announcement_po{
    position: relative;
}
.announcement_po_ab{
    position: absolute;
    right: 0;
    top: 10px;
}
.announcement_po_bott {
    bottom: -84px;
    width: 100%;
}
.select-group .announcement_po_wit{
    width: 180px;
}
.select-group .announcement_po_wit input{
    width: 100%;
}

.el-form-itemstart_time_flot_l{
    flex: 1;
    margin-right: 25px;
}
.el-form-itemstart_time_flot_r{
    flex: 1;
    margin-left: 25px;
}
.el-form-itemstart_time_flot_l .select-group-rq input{
    width: 100%;
    padding: 0 30px;
}
.el-form-itemstart_time_flot_r .select-group-rq input{
    width: 100%;
    padding: 0 30px;

}

.is-success_el-form-itemstart_time{
    padding: 15px 0;
    display: flex;
    margin-bottom: 30px;
}

.el-textarea{
    height: 125px;
    width: 100%;
}
.el-textarea_box h6{
    margin-bottom: 20px;
}
.el-textarea input::-webkit-input-placeholder{
    color: #c0c4cf;
}


.examine-list-boxmy_content{
   width: 100%;
   padding: 0 30px;
   margin-bottom: 30px;
}
.examine-list-boxmy_content_none{
    display: none;
}
.examine-list-boxmy_content_block{
    display: block;
}
.examine-list-boxmy_content_list-box{
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 30px;
}
.boxmy_content_list-box_xx_img{
    width: 35px;
    height: 35px;
    float: left;
    margin-right: 10px;
}
.boxmy_content_list-box_xx_img img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.boxmy_content_list-box_xx_title{
    margin-left: 45px;
}
.boxmy_content_list-box_xx_title_realname,
.boxmy_content_list-box_title{
    font-size: 14px;
    color: #333;
    margin-bottom: 10px;
}
.boxmy_content_list-box_xx_title_start_time{
    font-size: 12px;
    color: #999;
}
.boxmy_content_list-box_content{
    margin-top: 10px;
    color: #999;
    font-size: 12px;
    line-height: 18px;
    white-space: pre-wrap;
    word-wrap: break-word;
    background-color: #f0f7ff;
    padding: 15px;
    border-radius: 3px;
    color: #333;
    letter-spacing: .5px;
}
.boxmy_content_list-box_xx{
    margin-bottom: 20px;
}