.center{
    display: flex;
    width: 100%;
    background: #f5f6f9;
    padding: 15px;
    padding-right: 0;
}
.center::-webkit-scrollbar{
    width:6px; 
    height:2px;
    background:#ccc; 
    border-radius: 4px; 
}
.center::-webkit-scrollbar-thumb{
    display:block; 
    width:6px; 
    margin:0 auto; 
    border-radius: 4px; 
    background:rgb(129, 165, 167);
}
.site-calendar-demo-card{
    margin: 0 !important;
}
.site-calendar-demo-card{
    width: 100% !important;
}
.centerx{
    flex: 2;
    min-height: 600px;
    /* height: 100%; */
    padding: 20px;
    background: white;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-right: 20px;
}
.cent{
    flex: 1;
}
.left{
    background: white;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.right{
    position: relative;
    background: white;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 20px;
}
.right .ant-picker-calendar-header {
    padding:12px 8px !important;
    position: static;
}
.left_top{
    margin: 15px;
    border-bottom: 1px solid #ccc;
}
.left_top p{
    font-size: 14px;
}
.left_bt{
    width: 140px;
    height: 126px;
    margin: 30px 120px;
}
.left_bt img{
    width: 100%;
    height: 100%;
}
.left_tb{
    text-align: center;
}
.left_tb p{
    margin-top: 20px;
}
.left_bottom span{
    color: #3e84e9;
}
.site-calendar-demo-card{
    border: 1px solid #f0f0f0;
    border-radius: 2px;
    width: 100% !important;
  }
.btn{
    position: absolute;
    top: 12px;
    left: 20px;
}
.btn span{
    color: #3e84e9;
}
.scroll{
    width: 100%;
    height: 500px;
    overflow: auto;
}
.scroll::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}
.scroll::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
.centerx>div{
    width: 100%;
}
.txtb{
    width: 35px;
    height: 35px;
    border-radius: 17.5px;
    float: left;
    margin-right: 15px;
}
.txtb-nr{
    float: left;
    width: calc(100% - 50px);
}
.work-h1 h1{
    float: left;
    margin-right: 10px;
    color: #333;
    font-size: 15px;
}
.work-p1{
    width: 120px;
    float: left;
    color: #666;
    font-size: 13px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.work-p2{
    float: right;
    font-size: 13px;
}
.work-p2 .iconfont{
    color: #fecd51;
    font-weight: bold;
}
.work-h1 p{
    margin-bottom: 0;
}
.work-h1 h1{
    margin-bottom: 0;
}
.work-sj{
    color: #999;
    font-size: 12px;
}
.work-plnr{
    margin: 20px 0 0;
    color: #3e84e9;
    white-space: pre-wrap;
    word-wrap: break-word;
    letter-spacing: .5px;
    line-height: 18px;
    background-color: #f4f7fd;
    padding: 10px;
    border-radius: 2px;
}
.work-ztbj{
    padding: 20px 0;
    border-bottom: 1px solid #f1f1f1;
}
.cent_box{
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #F5F6F9;
    top: 0;
    left: 0;
    z-index: 9999;
}
.cent_box_hidden{
    display: none;
}
.create_icon{
    width: 40px;
    height: 40px;
    padding: 10px;
    box-sizing: border-box;
    position: absolute;
    color: #CDCDCD !important;
    right: 22%;
    top: 35px;
    font-size: 40px !important;
    z-index:11;
}
.create_bcqx2{
    z-index: 1;
}
.work_renwu_show{
    width: 65%;
    height: 92%;
    position: fixed;
    background-color: #fff;
    z-index: 11;
    top: 67px;
    right: 0;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
}
.work_renwu_hidden{
    display: none;
}
.work_richeng_show{
    width: 400px;
    position: fixed;
    background-color: #fff;
    z-index: 11;
    top: 30%;
    right: calc(50% - 200px);
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    padding-top: 30px;
    box-sizing: border-box;
}
.work_richeng_name{
    font-size: 16px;
    padding-left:25px;
    box-sizing: border-box;
    margin-bottom: 20px;
}
.work_richeng_title{
    padding: 0 30px 30px 30px;
    box-sizing: border-box;
}
.work_richeng_title p{
    margin-bottom: 15px;
}
.work_shenpi_box{
    width: 100%;
    padding: 10px 30px;
    box-sizing: border-box;
}
.work_shenpi_box_title{
    height: 60px;
    line-height: 60px;
}
.work_shenpi_box_name p{
    width: 50%;
    float: left;
}
.work_shenpi_box_name{
    height: 40px;
    line-height: 40px;
    margin-top: 10px;
    color: #777;
    margin-bottom: 20px;
}
.work_shenpi_box_name p span{
    margin-left: 25%;
    color: #333;
}
.work_shenpi_box_nma{
    margin: 20px 0;
    font-size: 13px;
    color: #3e84e9;
    padding-left: 13px;
    box-sizing: border-box;
}
.work_shenpi_box_xinxi{
    position: relative;
    padding-left: 10px;
    box-sizing: border-box;
}
.work_shenpi_box_xinxi span{
    position: absolute;
    left: 0;
    top: 4px;
    width: 2px;
    height: 14px;
    background-color: rgb(70, 205, 207);
}
.work_shenpi_box_img{
    padding-left: 13px;
    box-sizing: border-box;
}
.work_shenpi_box_img{
    height: 80px;
    line-height: 80px;
}
.work_shenpi_box_name_img{
    float: left;
    width: 40px;
    height: 40px;
    margin-right: 15px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-top: 20px;
    position: relative;
    font-size: 12px;
    color: #ccc;
}
.work_shenpi_box_name_img span{
    position: absolute;
    bottom: -20px;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    font-size: 12px;
    text-align: center;
    line-height: 16px !important;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.work_shenpi_box_name_img_span1{
    background-color: #8ADFE1;
    left: 0;
}
.work_shenpi_box_name_img_span2{
    background-color: #FF6767;
    left: 0;
}
.work_shenpi_box_name_img_span3{
    width: 30px !important;
    left: 25px;
}
.work_shenpi_box_name_img_span4{
    width: 30px !important;
    left: 25px;
}
.work_shenpi_box_name_img img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.work_shenpi_box_name_title{
    float: left;
    margin-right: 20px;
    font-size: 16px;
    color: #333 !important;
}
.work_gonggao_show{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #F5F6F9;
    top: 0;
    left: 0;
    z-index: 11 ;
}
.work_gonggao_box{
    width: 65%;
    height: calc(100% - 80px);
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    background-color: #fff;
    margin: 40px auto;
    position: relative;
    padding: 20px;
    box-sizing: border-box;
    color: #303133;
}
.work_gonggao_hidden{
    font-size: 20px !important;
    color: #ccc !important;
    cursor: pointer;
    position: absolute;
    right: 20px;
    top: 20px;
}
.work_gonggao_connent{
    font-size: 16px;
    font-weight: normal;
}
.work_gonggao_time{
    color: #999;
    text-align: center;
    font-size: 12px;
    margin-top: 8px;
}
.work_gonggao_name{
    text-align: center;
}
.work_gonggao_title{
    margin-top: 20px;
    line-height: 24px;
    color: #333;
    padding: 0 20px;
    white-space: pre-wrap;
    word-wrap: break-word;
}