.statistics_box{
    width: 100%;
    height: 100%;
    padding: 15px;
    background-color: #f5f6f9;
}
.statistics_box h3{
    padding: 0 20px;
    color: #333;
    line-height: 60px;
    font-size: 18px;
}
.statistics_content_box{
    width: 100%;
    height: calc(100% - 75px);
    overflow: auto;
    background-color: #fff;
}
.statistics_section{
    border: solid 1px #e6e6e6;
    padding: 10px;
}
.statistics_section h6{
    font-size: 13px;
    color: #333;
}
.statistics_section h6 span{
    border-left-color: rgb(70, 205, 207);
    border-left-width: 2px;
    border-left-style: solid;
    height: 10px;
    margin-right: 10px;
    display: inline-block;
}
.vux-flexbox_content{
    padding: 10px 60px;
    display: flex;
}
.radial-progress-container{
    width: 120px;
    height: 120px;
    border: solid 10px #e7f2fa;
    border-radius: 50%;

    display: inline-block;
}
.radial-progress_box{
    width: 100%;
    position: absolute;
    top: 50%;
    margin-top: -60px;
    text-align: center;
    left: 0;
}
.radial-progress-container p{
    font-size: 12px;
    color: #666;
    margin-top: 30px;
}
.radial-progress-container div{
    margin-top: -10px;
}
.radial-progress-container em{
    font-size: 12px;
    color: #333;
}
.radial-progress-container span{
    font-size: 27px;
    color: #333;
    font-weight: 700;
}
.radial-progress-container_box{
    flex-grow: 1.4;
    flex-shrink: 1;
    flex-basis: 0%;
    text-align: center;
    position: relative;
}
.wanchenglv{
    margin-right: 10%;
}
.content-bar{
    flex: 2;
    text-align: center;
    height: 150px;
    position: relative;
}
.content-bar_box{
    position: absolute;
    left: 50%;
    bottom: 0;
    margin-left: -200px;
    width: 400px;
    display: flex;
}
.content-bar_small{
    flex: 1;
}
.content-bar_num{
    color: #4c4c4c;
}
.content-bar_xian{
    width: 100%;
    height: 1px;
    background-color: #ececec;
    margin: 5px 0;
}
.content-bar_task{
    color: #666;
    font-size: 12px;
}

.project-body{
    margin-bottom: 8px;
    position: relative;
}

.project-body-select{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 180px;
    height: 34px;
    line-height: 34px;
    border: solid 1px #e6e6e6;
    color: #333;
}
.project-body-select input{
    padding: 0 30px 0 8px;
    width: 100%;
}
.project-body-select span{
    position: absolute;
    right: 0;
    top: 0;
    width: 25px;
    line-height: 34px;
    text-align: center;
    color: #c7cbd2;
}
.project-body-select_show{
    position: absolute;
    bottom: -80px;
    left: 0;
    width: 100%;
    border: solid 1px #e4e7ed;
    background-color: #fff;
}
.project-body-select_show>div{
    font-size: 12px;
    padding: 0 20px;
}
.project-body-select_show>div:hover{
    background-color: #f5f7fa;
}
.has-gutter_box{
    border: solid 1px #e6e6e6;
}
.header_wrapper_box{
    width: 100%;
    display: flex;
}
.header_wrapper_box>div{
    flex: 1;
    background-color: #f5f5f5;
    padding: 12px 0;
    color: #909399;
    font-size: 14px;
    line-height: 22px;
    border-bottom: 1px solid #e6e6e6;
    border-right: 1px solid #e6e6e6;
}
.header_wrapper_box>div:last-child{
    border-right: none;
}
.header_text{
    width: 100%;
    padding: 0 10px;

}
.zanwushuju{
    height: 452px;
    line-height: 452px;
    text-align: center;
    color: #909399;
}