.officelog{
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
}
.officelog_box{
    width: 100%;
    height: 100%;
    background-color:#fff;
}
.officelog_box_header{
    margin: 0 15px 20px 0;
    padding: 15px 20px 5px;
    width: 100%;
    height: 54px;
    color: #333;
}
.officelog_daochu{
    float: right;
    margin-right: 45px;
}
.officelog_bottom{
    height: calc(100% - 74px);
    padding: 0 60px 20px;
    box-sizing: border-box;
}
.officelog_bottom_biaoge{
    width: 100%;
    height: 100%;
}
/* .officelog_bottom_biaoge> */
.office_biaoge_width>div{
    width: calc(20% - 1px);
}
.listcontract_box1_biaoge_box1>div>div:nth-child(2n){
    background-color: #FAFAFA;
}
.listcontract_box1_biaoge_box1>div>div:nth-child(2n+1){
    background-color: #fff;
}
/* officeapproval_biaoge_width */
.officeapproval_biaoge_width>div{
    width: calc(100% / 7);
}
.officeapproval_biaoge_biaoge{
    width: 100%;
    margin-top: 47px;

}
.officeapproval_biaoge_width{
    height: 47px;
}
.officeapproval_biaoge_biaoge>div>div{
    float: left;
    text-align: center;
    height: 47px;
    line-height: 47px;
    border-right: 1px solid #E6E6E6;
    border-bottom: 1px solid #E6E6E6;
    color: #659DED;
}
.officeapproval_biaoge_biaoge>div:nth-child(2n){
    background-color: #FAFAFA;
}
.officeapproval_biaoge_biaoge>div:hover{
    background-color: #ECF3FD;
}
.officeapproval_biaoge_biaoge>div>div:first-child{
    color: #333;
}