@font-face {
    font-family: 'iconfont';
    src: url('../font/iconfont.eot');
    src: url('../font/iconfont.eot?#iefix') format('embedded-opentype'),
        url('../font/iconfont.woff2') format('woff2'),
        url('../font/iconfont.woff') format('woff'),
        url('../font/iconfont.ttf') format('truetype'),
        url('../font/iconfont.svg#iconfont') format('svg');
}

.iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.through_box {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
}

.through {
    width: 70%;
    border: 1px solid #e6e6e6;
    background: #fff;
    height: 100%;
    border-radius: 4px;
    position: relative;
    overflow: hidden;
    /* overflow-y: auto ; */

}

/* .back{
    width: 100%;
    background-color: #fff;
    z-index: 9;
    position: absolute;
    top: 0;
    left: 0;
} */

.title {
    margin: 0 15px 15px;
    border-bottom: 1px solid #e6e6e6;
}

.title li {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    padding: 0 20px;
    font-size: 14px;
}

.title .title_color {
    color: #3e84e9;
    position: relative;
}

.title .title_color span {
    position: absolute;
    width: 28px;
    transform: translateX(0px);
    bottom: -1px;
    left: 20px;
    height: 2px;
    background-color: #3e84e9;
    z-index: 1;
}

.title_block {
    display: none;
    min-height: 0;
}
.input_v{
    position: relative;
    height: 100%;
}
.input_b{
    position: relative;
}

.input_box {
    width: 230px;
    margin: 10px 30px;
    position: relative;
    font-size: 13px;
    display: inline-block;
    z-index: 9999;
}

.input_box input {
    padding-left: 38px;
    height: 34px;
    line-height: 34px;
    padding-right: 8px;
    width: 100%;
    color: #bfc3cc;
    border: 1px solid #ddd;
}

.input_box input:focus {
    border: 1px solid #3e84e9;
    color: #000;
}

.input_box span {
    position: absolute;
    left: 5px;
    top: 0;
    width: 25px;
    line-height: 34px;
    text-align: center;
}
.input_box .iconfont{
    color: #bfc3cc;
}
.sj{
    position: absolute;
    bottom: -65px;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999;
}
.display-none{
    display: none;

}

.userlist_box {
    width: 100%;
    height: 100%;
}

.userlist {
    padding: 0 30px ;
    /* background-color: red; */
    
}
.overflow{
    overflow:auto;
    height: 720px;
}
.userlist::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
.userlist::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}
.tab-pane{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 20px;
    background-color: #fff;
}


.list-right .list-right-span .iconfont {
    color: #bfc3cc !important;
    margin-right: 3px;
    font-size: 16px;
}


.staff{
    margin-top: 15px;
    font-size: 14px;
    vertical-align: middle;
}
.k-realname{
    display: inline-block;
    color: #333;
    font-size: 14px;
    margin-right: 15px;
    width: 96px; 
}
.list-right{
    display: inline-block;
    font-size: 0;
}
.list-right span{
    color: #777;
    font-size: 14px;
}
.list-right-span{
    margin-right: 15px;
}








.el-collapse-item__header{
    height: 48px;
    line-height: 48px;
    background-color: #fff;
    color: #303133;
    border-bottom: 1px solid #ebeef5;
    font-size: 13px;
    position: relative;
}
.el-collapse-item__header_title{
    padding-left: 25px;
    color: #999;
}
.el-collapse-item__header .anticon{
    position: absolute;
    left: 0;
    color: #999;
    font-size: 11px;
    line-height: 48px;
}
.marg{
    margin: 0 10px;
}


.fun_down .anticon{
    position: absolute;
    left: 10px;
    color: #999;
    font-size: 11px;
    line-height: 48px;
    transform: rotate(45deg);
    top: -2px;
}




.no{
    display: none;
}