.Jobstask{
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
}
.Jobstask_box{
    width: 100%;
    height: 100%;
}
.Jobstask_box h3{
    padding: 0 20px;
    color: #333;
    height: 60px;
    line-height: 60px;
    width: 100%;
    box-sizing: border-box;
    font-size: 18px;
}
.Jobstask_box_div{
    width: 100%;
    height: 136px;
    color: #333;
}
.Jobstask_box_div>div{
    height: 100%;
    background-color: #fff;
    float: left;
    width: 24%;
    padding: 10px;
    box-sizing: border-box;
    margin-right: 1%;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    position: relative;
}
.Jobstask_title{
    width: 100%;
    padding: 10px 3px 7px;
    box-sizing: border-box;
}
.Jobstask_title_p1{
    margin-bottom: 5px;
}
.Jobstask_title_p1 span{
    color: #999;
    margin-left: 10px;    
}
.Jobstask_title_p2{
    height: 5px;
    width: 100%;
    border-radius: 8px;
    background-color: #EBEEF5;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}
.Jobstask_newbox{
    position: absolute;
    bottom: 10px;
    left: 15px;
    font-size: 13px;
    color: #999;
    width: calc(100% - 15px);
}
.Jobstask_newbox span{
    color: #3E84EA !important;
    margin-right: 5px;
    font-size: 13px !important;
}
.project_color_margin{
    margin-right: 10px;
}
.project_new_box_width{
    width: 45%;
    float: none;
}
.project_new_box_p{
    margin-bottom: 0;
}
.project_new_box_jia_name{

}
.project_new_box_jia{
    height: 200px;
    bottom: 48px;
    left: 20%;
}
.create_div_p>span{
    font-size: 20px;
    color: #CCCCCC;
}
.project_new_box_jia_name_h1{
    height: 152px;
}
.project_new_box_jia_name_h2{
    height: 200px;
}