.colleagues-list-box{
    padding: 5px;
    height: 248px;
    overflow-y: auto;
}
.colleagues-list-box::-webkit-scrollbar{
    width: 7px;
    height: 7px;
}
.colleagues-list-box::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}
.colleagues-list{
    padding: 5px;
    display: block;
    margin-left: 0;
}
.colleagues-list-input{
    float: left;
    margin-right: 8px;
    position: relative;
}
.colleagues-list-input input[type="checkbox"]{
    -webkit-appearance: none;
    vertical-align:middle;
    margin-top:0;
    background:#fff;
    border:#ddd solid 1px;
    border-radius: 3px;
    width: 14px;
    height: 14px;
}
.colleagues-list-input input[type="checkbox"]:checked {
    background: #3190e8;
}
.colleagues-list-input input[type=checkbox]:checked::after{
    content: '';
    top: 9px;
    left: 3px;
    position: absolute;
    background: transparent;
    border: #fff solid 1px;
    border-top: none;
    border-right: none;
    height: 6px;
    width: 9px;
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg); 
    transform: rotate(-45deg);
}
.colleagues-list-cont{
    float: left;
}
.colleagues-list-img{
    width: 24px;
    height: 24px;
    float: left;
    margin-right: 8px;

}
.colleagues-list-img img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.colleagues-list-title{
    line-height: 19px;
    font-size: 13px;
    float: left;
}