.listcontract{
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
}
.listcontract_box1{
    width: 100%;
    height: 100%;
    background-color: #fff;
    overflow: auto;
}
.listcontract_box_header_box1{
    width: 200px;
    height: 34px;
    margin-right: 20px;
}
.listcontract_box1_header{
    width: 100%;
    height: 54px;
    padding: 15px 20px 5px;
    box-sizing: border-box;
}
.listcontract_box1_header>div{
    float: left;
}
.listcontract_box_header_box2{
    width: 120px;
    margin-right: 20px;
}
.listcontract_box1_header_box3{
    background-color: #3E84E9;
    color: #fff;
    height: 31px;
    line-height: 31px;
    padding: 0 12px;
}
.listcontract_box1_header_title{
    padding: 10px 20px 5px;
    color: #333;
    width: 100%;
}
.listcontract_box1_header_paih{
    width: 100%;
    padding: 20px 98px 40px;
    height: 460px;
}
.list_connent{
    width: 100%;
    height: 100%;
}
.listcontract_box1_biaoge{
    width: 100%;
    height: 420px;
    padding: 0 60px 20px;
    box-sizing: border-box;
}
.listcontract_box1_biaoge_div{
    width: 100%;
    height: 100%;
    border: 1px solid #e6e6e6;
    overflow: auto;
    position: relative;
}
.listcontract_box1_biaoge_box1{
    width: 100%;
    height: 1000px;
    margin-top: 47px;
}
.listcontract_box1_biaoge_box1>div{
    width: calc(25% - 1px);
    float: left;
    text-align: center;
    line-height: 47px;
    /* border-right: 1px solid #e6e6e6; */
}
.listcontract_box1_biaoge_box1>div>div{
    border-right: 1px solid #e6e6e6;
    height: 47px;
    border-bottom: 1px solid #e6e6e6;
}
.listcontract_box1_biaoge_dingwei{
    width: 100%;
    border: 1px solid #e6e6e6;
    height: 47px;
    line-height: 47px;
    position: absolute;
    top: 0;
    left: 0;
}
.listcontract_box1_biaoge_dingwei>div{
    width: calc(25% - 1px);
    border-right: 1px solid #e6e6e6;
    line-height: 47px;
    float: left;
    text-align: center;
}