.log{
    width: 70%;
    height: 100%;
    padding: 15px;
}
.log-nr{
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding-bottom: 20px;
}
.ant-tabs-nav{
    /* margin: 0 20px 20px 20px !important; */
    padding: 0 20px;
}
.ant-space-vertical{
    margin-left: 10px;
}
.log-btm-ssj p>span{
    color: rgb(153, 153, 153);
    font-size: 12px;
    margin-left: 10px;
}
.log-ul{
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #f0f0f0;
}
.log-xrz-gd{
    width: 100%;
    overflow: auto;
    height: 500px;
}
.log-xrz-gd::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
.log-xrz-gd::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}
.log-nr{
    position: relative;
}
.log-xrz-btn{
    position: absolute;
    top: 10px;
    right: 10px;
    color: #fff;
    background-color: #3e84e9;
    border-color: #3e84e9;
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid #ddd;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    margin: 0;
    transition: .1s;
    font-weight: 500;
    -webkit-user-select: none;
    padding: 8px 12px;
    font-size: 13px;
    border-radius: 2px;
    z-index: 9;
}
.log-p span{
    width: 95px;
    text-align: left;
    display: inline-block;
    color: #999;
    font-size: 13px;
}
.log-nr>div{
    height: 100%;
}
.ant-tabs-content{
    height: 100%;
}
.log-ztct{
    height: 100%;
    overflow: auto;
}
.log-ztct::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
.log-ztct::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}
.log-ct{
    border: solid 1px #e6e6e6;
    margin-bottom: 20px;
    border-radius: 4px;
    margin: 0 30px;
    margin-bottom: 20px;
}
.log-ct-nr-oo .log-wd{
    color: #3e84e9;
}
.log-btn{
    background: #f4f7fd;
    height: 40px;
    line-height: 40px;
    color: #ccc;
    text-align: right;
    padding-right: 20px;
}
.log-top{
    padding: 20px;
}
.log-btn button{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #ddd;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    margin: 0;
    -webkit-transition: .1s;
    transition: .1s;
    font-weight: 500;
    -webkit-user-select: none;
    padding: 8px 12px;
    font-size: 13px;
    border-radius: 2px;
    color: #fff;
    background-color: #3e84e9;
    border-color: #3e84e9;
}
.log-ct-img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    overflow: hidden;
    float: left;
    margin-right: 10px;
}
.log-ct-nr{
    float: left;
    width: calc(100% - 45px);
}
.log-ct-nr-oo-p{
    float: left;
}
.log-ct-img img{
    width: 100%;
    height: 100%;
}
.log-ct-nr-oo{
    float: left;
    width: 100%;
    margin-bottom: 7px;
}
.log-ct-nr-oo-p{
    font-size: 15px;
    margin: 0 10px 0 0;
    color: #333;
    line-height: 16px;
    font-size: 16px;
}
.log-ct-nr-oo span{
    color: rgb(204, 204, 204);
    font-size: 12px;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    font-family: MicrosoftYaHei,Avenir,Helvetica,Arial,sans-serif;
    line-height: 16px;
}
.log-ct-nr-oo-sj p{
    float: left;
    margin-right: 10px;
}
.log-ct-nr-oo-sj{
    color: #999;
    font-size: 12px;
}
.log-btm-img{
    width: 25px;
    height: 100%;
    overflow: hidden;
    border-radius: 50%;
    float: left;
}
.log-btm-img img{
    width: 100%;
}
.log-btm-ssj{
    margin-left: 45px;
}
.log-btm{
    border-top: solid 2px #e6e6e6;
    padding: 20px;
}
.log-ct-nr-oo-p span{
    color: #999;
    font-size: 12px;
    margin-left: 10px;
}
.log-btm-it{
    margin-bottom: 20px;
    border-bottom: 2px solid #e6e6e6;
    padding-bottom: 10px;
}
.log-btm-it:last-child{
    border: none;
    padding-bottom: 0 !important;
    margin-bottom: 0;
}
.log-ztp{
    font-size: 14px;
    color: #ccc;
    line-height: 40px;
    text-align: center;
}


.log-nr_hight{
    height: 270px;
    overflow-y: auto;
    bottom: -280px;
    width: 100%;
}
.log-nr_hight_bot{
    width: 100%;
    bottom: -135px;
}

.log-nr_hight::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
.log-nr_hight::-webkit-scrollbar-thumb {
    background: #c7d1da;
    border-radius: 3.5px;
}
.select-group .log-nr_input_left_wigth{
    width: 140px;
}
.log-nr_input_left_wigth_margin{
    margin-right: 20px;
}
.log_margin_top{
    margin-top: 20px;
}
