.salesfunnel_box{
    width: 100%;
    height: 100%;
    background-color: #f5f6f9;
}
.salesfunnel_content_box{
    padding: 15px;
    height: 100%;
}
.salesfunnel_content{
    background-color: #fff;
    height: 100%;
    position: relative;
}
.salesfunnel_content_title{
    padding: 15px 20px 5px;
    margin-right: 15px;
    line-height: 34px;
    color: #333;
}
.salesfunnel_title_years{
    width: 100%;
    margin-right: 15px;
    position: relative;
    border-radius: 3px;
    border: 1px solid #ddd;
    margin-bottom: 10px;
}
.salesfunnel_title_years input{
    width: 100%;
    padding-right: 30px;
    padding-left: 8px;
}
.salesfunnel_content_title span{
    position: absolute;
    right: 5px;
    top: 5px;
    color: #c0c4cc;
}
.salesfunnel_title_classification{
    width: 100%;
    position: relative;
    border-radius: 3px;
    border: 1px solid #ddd;
    padding: 0 30px 0 8px;
    margin-right: 20px;
    margin-bottom: 10px;
}
.salesfunnel_title_search{
    float: left;
    color: #fff;
    background-color: #3e84e9;
    border-color: #3e84e9;
    width: 50px;
    text-align: center;
    border-radius: 3px;
}
.salesfunnel_title_years_box{
    position: relative;
    width: 200px;
    margin-right: 15px;
    position: relative;
    border-radius: 3px;
    float: left;

}
.years_list_box{
    height: 250px;
    overflow: auto;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    position: absolute;
    width: 100%;
    z-index: 10;
    background-color: #fff;
}
.years_list>div{
    padding: 0 30px;
    position: relative;
}
.years_list>div:hover{
    background-color: #f5f7fa;
    color: #409eff;
    padding: 0 30px;
}
.years_list>div span{
    position: absolute;
    left: 15px;
    top: 15px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #409eff;
    display: none;
}
.salesfunnel_title_staff_box{
    float: left;
    width: 120px;
    margin-right: 20px;
}
.staff_list_box{
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    width: 120px;
    position: absolute;
    z-index: 10;
    background-color: #fff;
}

.staff_list>div{
    padding: 0 20px;
}
.staff_list>div:hover{
    padding: 0 20px;
    background-color: #f5f7fa;
    color: #409eff;
}
.personnel_list_box{
    height: 270px;
    overflow: auto;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    font-size: 12px;
    width: 120px;
    position: absolute;
    z-index: 10;
    background-color: #fff;
}
.personnel_list>div{
    padding: 0 20px;
}
.personnel_list>div:hover{
    padding: 0 20px;
    background-color: #f5f7fa;
}
.system_list_box{
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    width: 120px;
    z-index: 10;
    position: absolute;
    background-color: #fff;
}
.system_list_box>div{
    padding: 0 20px;
}

.salesfunnel_content_salesfunnel_box{
    width: 100%;
    text-align: center;
}
.salesfunnel_content_salesfunnel_box>div{
        position: relative;
        width: 200px;
        margin:  0 auto;
}
.salesfunnel_content_salesfunnel_box>div p{
    line-height: 100px;
}
.salesfunnel_content_salesfunnel_box>div span{
    position: absolute;
    left: 50%;
    top: 53px;
    width: 20px;
    height: 1px;
    margin-left: -75px;
}
.salesfunnel_content_salesfunnel_box .salesfunnel_1 span{
    background-color: #6ca2ff;
    
}
.salesfunnel_content_salesfunnel_box .salesfunnel_1 p{
    color: #6ca2ff;
}
.salesfunnel_content_salesfunnel_box .salesfunnel_2 span{
    background-color: #6ac9d7;
    
}
.salesfunnel_content_salesfunnel_box .salesfunnel_2 p{
    color: #6ac9d7;
}
.salesfunnel_content_salesfunnel_box .salesfunnel_3 span{
    background-color: #72dca2;
    
}
.salesfunnel_content_salesfunnel_box .salesfunnel_3 p{
    color: #72dca2;
}
.salesfunnel_content_salesfunnel_box .salesfunnel_4 span{
    background-color: #5eea9b;
    
}
.salesfunnel_content_salesfunnel_box .salesfunnel_4 p{
    color: #5eea9b;
}
.salesfunnel_content_salesfunnel_box .salesfunnel_hover{
    position: absolute;
    right: 50%;
    top: 63px;
    padding: 5px 0;
    background-color: #6f6f6f;
    line-height: 20px;
    width: 100px;
    color: #fff;
    border-radius: 5px;
    margin-right: -150px;
    display: none;
}
.salesfunnel_1:hover p,
.salesfunnel_2:hover p,
.salesfunnel_3:hover p,
.salesfunnel_4:hover p{
    font-size: 20px;
    /* text-align: right; */
}
.salesfunnel_1:hover .salesfunnel_hover,
.salesfunnel_2:hover .salesfunnel_hover,
.salesfunnel_3:hover .salesfunnel_hover,
.salesfunnel_4:hover .salesfunnel_hover{
    display: block;
}

.salesfunnel_content_form{
    width: 100%;
    line-height: 47px;
}
.salesfunnel_content_form_title_box{
    color: #909399;
    width: 100%;
    display: flex;
    border: solid 1px #e6e6e6;
}
.salesfunnel_content_form_title{
    padding: 0 60px 20px;
}
.salesfunnel_content_form_title_box div{
    flex: 1;
    border-right:  1px solid #e6e6e6;
    text-align: center;
}   
.salesfunnel_content_form_title_box>div:last-child{
    border-right: none;
}
.from_list_box{
    width: 100%;
    height: 352px;
    border: solid 1px #e6e6e6;
    border-top: none;
}
.from_list_box_content{
    display: flex;
}
.from_list_box_content>div{
    flex: 1;
    display: inline-block;
    text-align: center;   
    border-bottom: solid 1px #e6e6e6;
    border-right: solid 1px #e6e6e6;

}
.from_list_box_content:nth-child(2n){
    background-color: #fafafa;
}
.from_list_box_content:hover{
    background-color: #fafafa;
}
.from_list_box_content>div:last-child{
    border-right: none;
}

.salesfunnel_list_content{
    height: calc(100% - 54px);
    overflow: auto;
    position: absolute;
    top: 54px;
    left: 0;
    width: 100%;
}

.from_list_zwsj{
    line-height: 352px;
    text-align: center;
    width: 100%;
    display: block;
    color: #909399;
}

.sheet_box{
    width: 100%;
    padding: 20px 10% 40px;
    height: 400px;
}
.table_text{
    margin-top: 20px;
    background-color: rgba(230,162,60,.1);
    border-color: rgba(230,162,60,.2);
    color: #e6a23c;
    height: 32px;
    line-height: 30px;
    font-size: 12px;
    border: 1px solid rgba(62,132,233,.2);
    border-radius: 4px;
    width: 250px;
    text-align: center;
}

.productssales_box{
    width: 100%;
    overflow-y: hidden;
}
.productssales_cont_box{
    width: 100%;
    height: 100%;
    margin-top: 20px;
    padding: 0;
}
.productssales_constbox{
    border: solid 1px #e6e6e6;
}
.productssales_content_form{
    height: 100%;
}
.productssales_content_form_title_box{
    height: calc(100% - 50px);
}
.product_classification_sales{
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    line-height: 100%;
    position: absolute;
    width: 100%;
    line-height: 40px;
    top: 50%;
    left: 0;
    margin-top: -20px;
    color: #333333;
}


.browserperformance_list_box{
    height: 352px;
    /* overflow: hidden; */
    overflow-y: auto;
}
.salesfunnel_content_form_title_box .browserperformance_list_tite_line{
    width: 5px;
    flex: none;
}
.ant-space-vertical{
    margin-left: 0;
}